body {
  min-width: 320px;
  margin: 0;
  font-family: 'Open Sans_my', sans-serif;
  color: #000;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
}
.row {
  margin-bottom: 30px;
}
.form {
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 30px;
  background-color: #f0f2ef;
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;
}
.form__title {
  color: #b5b7b3;
}
.form__text {
  font-size: 12px;
  font-weight: bold;
  margin-bottom: 15px;
  text-align: left;
  color: #454A4A;
}
.form__star {
  font-size: 16px;
}
.form__label {
  display: block;
  text-align: left;
  color: #454A4A;
}
.form__item {
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;
  width: 100%;
  font-size: 16px;
  color: #454A4A;
  border: 1px solid #b5b7b6;
  border-radius: 3px;
}
.form__button {
  width: 100%;
  background-color: #cbceca;
  border: none;
  color: #454A4A;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 3px;
}
.form__city-list {
  list-style: none;
}
.form__city-item {
  display: inline-block;
  background-color: #dee0e2;
  padding: 5px;
  border-radius: 3px;
  margin-bottom: 5px;
  margin-right: 3px;
}
.second-form-page {
  margin-top: 30px;
  text-decoration: none;
  background-color: #cbceca;
  border: none;
  color: #454A4A;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 3px;
}
.second-form-page :visited {
  color: #454A4A;
}
@media (max-width: 767px) {
  width: 70%;
}
@media (max-width: 480px) {
  .form {
    width: 100%;
  }
}
