* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	overflow-x: hidden;
}
.row {
	margin-bottom: 30px;
}
.form {
	padding-left: 30px;
	padding-right: 30px;
	padding-bottom: 30px;
	background-color: #f0f2ef;
	text-align: center;
	margin-top: auto;
	margin-bottom: auto;
	&__title {
		color: #b5b7b3;
	}
	&__text {
		font-size: 12px;
		font-weight: bold;
		margin-bottom: 15px;
		text-align: left;
		color: #454A4A;
	}
	&__star {
		font-size: 16px;
	}
	&__label {
		display: block;
		text-align: left;
		color: #454A4A;
	}
	&__item {
		padding-top: 8px;
		padding-bottom: 8px;
		padding-left: 10px;
		padding-right: 10px;
		margin-bottom: 20px;
		width: 100%;
		font-size: 16px;
		color: #454A4A;
		border: 1px solid #b5b7b6;
		border-radius: 3px;
	}
	&__button {
		width: 100%;
		background-color: #cbceca;
		border: none;
		color: #454A4A;
		padding-top: 8px;
		padding-bottom: 8px;
		border-radius: 3px;
	}
	&__city-list {
		list-style: none;
	}
	&__city-item {
		display: inline-block;
		background-color: #dee0e2;
		padding: 5px;
		border-radius: 3px;
		margin-bottom: 5px;
		margin-right: 3px;
	}
}
.second-form-page {
	margin-top: 30px;
	text-decoration: none;
	color: #454A4A;
	background-color: #cbceca;
		border: none;
		color: #454A4A;
		padding-top: 8px;
		padding-bottom: 8px;
		border-radius: 3px;
	:visited {
		color: #454A4A;
	}

}
@media (min-width: 768px) and (max-width: 979px) {

}

@media (max-width: 767px) {
	width: 70%;
}

@media (max-width: 480px) {
	.form {
		width: 100%;
	}
}
